<template>
    <div ref="mapContainer"></div>
</template>

<script>
    import {Scene} from '@antv/l7'
    import {WorldLayer} from '@antv/l7-district'
    import {GaodeMap} from '@antv/l7-maps'
    import axios from 'axios'

    export default {
        name: 'World',
        created() {
            axios.get('http://127.0.0.1:1024/worldCovid19Data').then(res=>{
                if(res !=null && res.data.success==true){
                    this.worldData=res.data.data.data
                }
            })
            this.$nextTick(() => {
                this.el = this.$refs.mapContainer
                this.scene = new Scene({
                    id: this.el,
                    map: new GaodeMap({
                        center: [116.2825, 39.9],
                        pitch: 0,
                        style: 'dark',
                        zoom: 0,
                        minZoom: 0,
                        maxZoom: 5
                    })
                })

                this.scene.on('loaded', () => {
                    this.layer = new WorldLayer(this.scene, {
                        data: this.worldData,
                        joinBy: ['NAME_CHN', 'name'], // 字段映射
                        fill: {
                            color: {
                                field: 'NAME_CHN',
                                values: [
                                    '#f3f3f3',
                                    '#fff2cf',
                                    '#fdd2a0',
                                    '#ff8c71',
                                    '#e64b45',
                                    '#bd1316'
             ]
                            }
                        },
                        bubble: { // 泡泡
                            enable: true, // 泡泡显示的开关
                            size: { // 泡泡的尺寸
                                field: 'confirm', // 关联字段
                                values: [10, 25] // 大小区间
                            },
                            color: {
                                field: 'confirm', // 颜色关联的字段
                                values: (value) => { // 颜色的回调函数
                                    return value > 5000000 ? '#ff0003' :
                                        value > 1000000 ? '#ffada5' :
                                            value > 100000 ? '#e4a0ff' : '#a3c0ff'
                                }
                            },
                            style: {
                                opacity: 0.6 // 设置透明度
                            }
                        },
                        stroke: '#ccc',
                        label: {
                            enable: true,
                            textAllowOverlap: false,
                            field: 'NAME_CHN'
                        },
                        popup: {
                            enable: true,
                            Html: props => {
                                // props 中的参数 应该使用 typeof 去判断是否存在

                                return `
                                <span>
                                    ${props.NAME_CHN}: <br/>
                                     <span style="color: #fe7e07">确诊人数：</span><span style="color: red">${typeof props.confirm !== 'undefined' ? props.confirm : '无数据'}</span><br/>
                                     <span style="color: #d31b29">死亡人数：</span><span style="color: red">${typeof props.dead !== 'undefined' ? props.dead : 0}</span>
                                </span>`
                            }
                        }
                    })

                })
            })
        },
        data() {
            return {
                el: null,
                layer: null,
                scene: null,
                worldData:null,
            }
        }
    }
</script>

